.resume {
  .info-section {
    width: 20%;
  }
  .content-area {
    background: white;
    margin-left: 20%;
    width: 80%;
    min-width: 300px;
    height: 100%;

    overflow: hidden;
  }
  h1 {
    font-size: 2rem;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.5rem;
    margin-bottom: 55px;
  }

  .xp-section {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 25px;
    // row-gap: 30px;
    max-width: 1000px;
    .xp-box {
      padding: 30px;
      margin: 15px;

      -webkit-box-shadow: 0 20 74px 6px rgba(0, 0, 0, 0.15);
      -moz-box-shadow: 0 20 74px 6px rgba(0, 0, 0, 0.15);
      box-shadow: 0 20 74px 6px rgba(0, 0, 0, 0.15);
      border-radius: 25px;
    }
    h5 {
      font-size: 1.3rem;
      font-weight: 500;
      margin-bottom: 0.8rem;
    }
    p {
      font-size: 1rem;
      font-weight: 300;
      margin-bottom: 1rem;
      line-height: 1.4rem;
    }
  }
}
